                        <div id="Conclusion" className="ProjectSectionEnd">
                            <Fade keyframes={fadeUp} duration="1200" triggerOnce={true}>
                                <div>
                                    <h2>Takeaways and Next Steps</h2>
                                    <p>Given this project was one of the more demanding assignments of the semester, I had the opportunity to become more familiar with interactive prototyping in Figma, collaborate with others to both plan, execute, and iterate upon the required project components, as well as develop some insight into designing for real-world applications with users as the focus.</p>
                                    <ul className="bullets">
                                        <li>Interactive prototyping in Figma</li>
                                        <ul>
                                            <li>Unlike previous assignments, this project required that we push ourselves a little further to consider more dynamic components like motion design and user flows. These aspects largely shape the user experience in that the prototype is expected to respond and bend to what the user ultimately needs, and Figma was a vital tool in being able visualize these elements without the need for any code. </li>
                                            <li>Such an application allows for more flexibility in iterating upon our prototypes prior to deployment, where the process of changing design choices tends to be more strenuous (speaking from personal experience).</li>
                                        </ul>
                                        <li>Planning, designing, and iterating as a group and class</li>
                                        <ul>
                                            <li>Having been used to working through design choices on my own, the experience of talking through and synthesizing ideas as a team presented the opportunity for a wider range of ideas to flow, as everyone had their own background and experience shaping their input. I found that lead us to making a prototype that felt more well-rounded and generalizable compared to something I would have constructed on my own.</li>
                                            <li>Attending our studio session and being able to recieve feedback in real-time from an even bigger pool of users (our peers) enabled us to also practice actively recieving and applying constructive criticism with regards to our design choices.</li>
                                            <li>One of the most valuable lessons I learned from working with my group was the need for cooperation, effective communication, and high motivation during our rapid iterative sprint of 2 weeks. Being able to talk through our ideas and choices to allocate tasks appropriately based on factors like areas of specialty and priority allowed us to complete this project within such a short and intense timeline.</li>
                                        </ul>
                                        <li>Designing real-world applications with a human-centered approach</li>
                                        <ul>
                                            <li>One of the lessons that stood out most to me in receiving feedback from our user testing was the assumption we had made in the final subtask of our task instructions. That was a great demonstration of how key it is to address any possible assumptions we as designers may make at any point in the design process, as early and as often as possible.</li>
                                            <li>Furthermore, the comment from user 3 about how "[their] own confusion made [them] frustrated" highlighted to me the importance of user-centered design and how we as designers are to prioritize the user's goal in mind with everything we test, build, and create.</li>
                                        </ul>
                                    </ul>
                                    <p>After testing, our team also reached out to the founders of Slingshot, who noted that they "especially liked the details click through for a bill that has already been saved on" and "thought the trends page was also interesting as we do not offer that as a service." It was a cool opportunity to try designing a product for an actual company, and even cooler to get some feedback on our work and how UIUX design is applied in a professional setting. ✮</p>

                                </div>
                            </Fade>
                        </div>
